import 'primeicons/primeicons.css';
import 'primereact/resources/themes/saga-blue/theme.css';
import 'primereact/resources/primereact.css';
import 'primeflex/primeflex.css';
import React, { Component } from "react";
import { Link } from "react-router-dom";
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import "./authentication.scss"

class ForgotPassword extends Component {
    state = {
       form: {
        username: '',
        password: ''
        },
        errors: {},
        message: '',
        redirect: ''
      };
    
    handleChange = (e) => {
        const { form, errors } = this.state;

        this.setState({
            form: { ...form, [e.target.name]: e.target.value },
            errors: { ...errors, [e.target.name]: null },
            loading: false,
            message: ''
          });
      };

    render() {
        return (
            <div className="p-fluid authentication">
                <div className="sign-container" id="component-App">
                    <p className="text-centered-signin">Forgot Your Password?</p>
                    <div className="p-field">
                        <label htmlFor="firstname1">Username or Email</label>
                        <InputText id="firstname1" type="text"/>
                    </div>
                    <Button label="Submit" />
                    <p className="text-footer" styles={{marginTop:"20px"}}>
                         <Link
                            className="color-link"
                            to="/">
                            Back to Login 
                        </Link>
                    </p>
                </div>
            </div>)
           
    }
}
export default ForgotPassword
